<template>
  <div class="home">
    <div id="theater">
      <video id="video1" src="../assets/video/vv1.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video2" src="../assets/video/vv2.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video3" src="../assets/video/vv3.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video4" src="../assets/video/vv4.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video5" src="../assets/video/vv5.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <video id="video6" src="../assets/video/vv6.mp4" controls="false" hidden playsinline webkit-playsinline></video>
      <div class="position-relative w-100">
        <div class="notice d-none position-absolute w-100 h-100">
          <div class="d-flex align-items-center h-100">
            <div class="w-100">
              <div class="notice-text mb-2 w-25 p-1 mx-auto" @click="playvideo = 'video2'">creative</div>
              <div class="notice-text mb-2 w-25 p-1 mx-auto" @click="playvideo = 'video3'">economic</div>
              <div class="notice-text mb-2 w-25 p-1 mx-auto" @click="playvideo = 'video4'">quality</div>
              <div class="notice-text mb-2 w-25 p-1 mx-auto" @click="playvideo = 'video5'">simple</div>
              <div class="notice-text w-25 p-1 mx-auto" @click="playvideo = 'video6'">colorful</div>
            </div>
            <!-- <p class="text-white h8">which one do you like?</p> -->
            </div>
        </div>
        <div class="notice2 position-absolute w-100 h-100">
          <div class="notice-text mb-3 w-25 p-2 mx-auto mb-3 w-25 p-2 mx-auto" @click="plaa">開始</div>
        </div>
        <canvas id="canvas"></canvas>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'home',
  data () {
    return {
      canvas: null,
      width: 0,
      height: 0,
      canvasInterval: null,
      playvideo: ''
    }
  },
  mounted () {
    var canvas = document.getElementById('canvas')
    var ctx = canvas.getContext('2d')
    var videoall = document.querySelectorAll('video')
    console.log(videoall)
    // set canvas size = video size when known
    videoall.forEach(video => {
      video.addEventListener('loadedmetadata', function () {
        canvas.width = video.videoWidth
        canvas.height = video.videoHeight
      })
      video.addEventListener('play', function () {
        // var $this = this;
        (function loop () {
          if (!video.paused && !video.ended) {
            document.querySelector('.notice').classList.add('d-none')
            ctx.drawImage(video, 0, 0, canvas.width, canvas.height)
            setTimeout(loop, 1000 / 30) // drawing at 30fps
          }
        })()
      }, 0)
      video.addEventListener('pause', function () {
        (function loop () {
          // ctx.drawImage(img, 0, 0)
          document.querySelector('.notice').classList.remove('d-none')
          // document.querySelector('.notice').remove.classList('d-none')
          console.log('pause')
        })()
      }, 0)
    })
  },
  watch: {
    playvideo () {
      console.log(this.playvideo)
      document.querySelectorAll('video').forEach(vv => {
        vv.pause()
      })
      switch (this.playvideo) {
        case 'video2':
          document.querySelector('#video2').play()
          break
        case 'video3':
          document.querySelector('#video3').play()
          break
        case 'video4':
          document.querySelector('#video4').play()
          break
        case 'video5':
          document.querySelector('#video5').play()
          break
        case 'video6':
          document.querySelector('#video6').play()
          break
        default:
          break
      }
    }
  },
  methods: {
    play () {
      document.querySelector('#video1').pause()
      document.querySelector('#video3').pause()
      document.querySelector('#video2').play()
    },
    play2 () {
      document.querySelector('#video2').pause()
      document.querySelector('#video1').pause()
      document.querySelector('#video3').play()
    },
    plaa () {
      document.querySelector('#video1').play()
      document.querySelector('.notice2').classList.add('d-none')
    }
  }
}
</script>

<style lang="scss">
  .home{
    canvas{
      width: 100% !important;
    }
    // background: red;
    .notice{
      background: rgba(0, 0, 0, 0.2)
      // top: 50%;
      // left: 50%;
    }
    .notice2{
      background: #3ccdd7ad;
      position: relative;
      .notice-text{
        position: absolute;
        top: 50%;
      }
    }
    .notice-text{
      font-size: 10px;
      border: 1px solid white;
      border-radius: 15px;
      color: white;
    }
    .notice-text:hover,.notice-text:active{
      font-size: 12px;
      border: 2px solid rgb(255, 238, 0);
      color: rgb(255, 238, 0);
      border-radius: 15px;
    }
  }
  video{
    // display: none;
  }
  *{
  font-family: PingFangTC;
  line-height: 1.3 !important;
  }
  .h8{
    font-size: 2rem;
  }
  .h7{
    font-size: 1.25rem;
  }
  .h6{
    font-size: 1.125rem;
  }
  .h5{
    font-size: 1rem;
  }
  .h4{
    font-size: 0.875rem;
  }
  .h3{
    font-size: 0.75rem;
  }
  .h2{
    font-size: 0.625rem;
  }
  .h1{
    font-size: 0.5rem;
  }
</style>



